.status {
    &__text {
        font-size: 18px;
        color: #04D4FB;
        border: 1px solid gray;
        padding: 5px;
        border-block-start-color: gold;
        border-block-end-color: #04D4FB;
        border-top-left-radius: 15px;
        border-bottom-right-radius: 15px;
        span {
            color: gray;
            font-style: italic; } }
    &__inputArea {
        width: 220px;
        height: 22px;
        padding: 5px;
        font-size: 17px;
        border-top-left-radius: 15px;
        border-bottom-right-radius: 15px;
        &:active, &:hover, &:focus {
            outline: 0;
            outline-offset: 0; } } }

